import React, {useState} from 'react';
import "../../css/Welcome.css"

function Welcome(props) {
    const [transX, setTransX] = useState(0)
    const [transY, setTransY] = useState(0)

    return (
        <div className={"newWelcomeOuter mainContents welcomeMain "+localStorage.getItem("ColorMode")}
             onMouseMove={(e) => chromaticAberration(e, setTransX, setTransY)}>
            <div>
            <span className={"newWelcome primary "+localStorage.getItem("ColorMode")}>
                Welcome To My Website<br/>
                [ETASH TYAGI]
            </span>
                <span className={"newWelcome secondary "+localStorage.getItem("ColorMode")}
                      style={{transform: `translate(${transX}px, calc(${transY}px - 50%))`}}>
                    Welcome To My Website<br/>
                [ETASH TYAGI]
            </span>
                <span className={"newWelcome tertiary "+localStorage.getItem("ColorMode")}
                      style={{transform: `translate(${-transX}px, calc(${-transY}px - 50%))`}}>
                    Welcome To My Website<br/>
                [ETASH TYAGI]
            </span>
            </div>
        </div>
    );
}

function chromaticAberration(e, setTransX, setTransY) {
    setTransX(Math.min(Math.max(e.movementX/4, -8), 8))
    setTransY(Math.min(Math.max(e.movementY/4, -8), 8))
}

export default Welcome;
